<<template> 
<main>
        <section class="content-header">
      <h1>Add Permission</h1>
    </section>
    <section class="content" ref="ref_load_permission">
      <div class="row">
        <div class="col-xs-12 col-sm-12 col-md-12 col-lg-4">
          <button type="button" class="permissionbox">
          <div class="pull-left cogs1"><i class="fa fa-cogs" aria-hidden="true"></i></div>
          <div class="pull-left cogs2">Add Permission Directly </div>
          <div class="clearfix"></div>
          </button>
        </div>
   
      </div>
      <div class="card box-warning">
        <div class="box-header with-border">
          <h3 class="box-title card-text">Apply Permissions</h3>
        </div>
        <div class="box-body">
          <div class="panel-group" id="accordion">
            
            <div class="panel panel-default" v-if="permissions" v-for="(per,x) in permissions">
              <div class="panel-heading">
                <h4 class="panel-title">
                  <label>
                    <input type="checkbox">
                  </label>
                  <a class="accordion-toggle" data-toggle="collapse" data-parent="#accordion" :href="'#collapseOne'+x"> {{per.name}} </a> </h4>
              </div>
              <div :id="'collapseOne'+x" class="panel-collapse collapse" :key="'ac'+x">
                <div class="panel-body">
                  <div class="form-group">

                    <div class="checkbox" v-for="(detail) in per.details" :key="detail.id+'-'+x" >
                      
                      <label>
                        <input type="checkbox" :value="detail.map_id" v-model="assigned_ids">
                        {{detail.name}} </label>
                    </div>
                     
                  </div>
                </div>
              </div>
            </div>


 
          </div>
          <div class="box-footer">
            <div class="text-center">
              <button type="button" class="btn link1 widthauto" @click="grand_permission">Grant Permission</button>
            </div>
          </div>
        </div>
      </div>
    </section>
    </main>
</template>
<script>
import {
    mapGetters
} from "vuex";
export default {
    name: "UserAddPermission",
    data() {
        return {
          assigned_ids:[],
          permissions:[],
          states:[],
          cities:[],
            editmode: false,
            step: 1,
            users: {},
            frm_user: new Form({
                id: '',
                organization: '',
                name: '',
                address_1: '',
                address_2: '',
                city: '',
                state: '',
                zip_code: '',
                country: 'united states',
                phone: '',
                email: '',
                fax: '',
                type: '',
                account_info: []
            })
        }
    },
    computed: {
        ...mapGetters(["errors"])
    },
        beforeCreate(){ 
          axios.get("api/user/role_permissions" ,{
            params:{user:this.$route.params.id}
          })
                .then((res) => {
                    this.permissions=res.data.data.permissions
                    this.assigned_ids=res.data.data.assigned_ids
                })
                 
        },
    methods: {
  

        grand_permission() {
            let cloaderd = this.$loading.show({
                container: this.$refs.ref_load_permission
            }); 
      
            axios.post('api/user/grand_permission', {
                  user: this.$route.params.id,
                  permissions: this.assigned_ids
                })
                .then((response) => {
                    cloaderd.hide()
                    if (response.data.success == true) { 
                        Toast.fire({
                            icon: 'success',
                            title: response.data.message
                        }); 
                    }
                })
                .catch(() => {
                    cloaderd.hide();
                }) 
        },



    },
    mounted() {
 
    },
    created() {

 
    },
    watch: {
 
    }
}
</script>